body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header-title {
  padding: 10px;
  margin: 0;
  text-align: center;
  background: #0f0e17;
  color: #f25f4c;
  font-size: 20px;
}

.Header-container {
  margin: 0;
}

.hidden {
  display: none;
}

.new-job-section {
  background-color: #0f0e17;
  width: 60%;
  /* margin-left: 15px; */
}

.job-input-name {
  width: 70%;
  height: 40px;
}

.job-input-payment {
  width: 20%;
  height: 40px;
  float: right;
}

.description-container {
  /* background-color: red; */
  justify-content: center;
  display: flex;
  height: 100px;
  padding-top: 10px;
}

.description-search {
  width: 100%;
  height: 95%;
}

.finished-title {
  color: white;
  text-align: center;
}

/* FINISHED JOB SECTION */

.finished-job-section {
  background-color: #0f0e17;
  /* text-align: center; */
  /* display: flex; */
  justify-content: center;
  align-items: center;
}

.job {
  /* margin-top: 30px; */
  /* margin-bottom: 20px; */
  border-bottom: 1px solid white;
  align-items: center;
  display: block;
}

.job-name-and-payment {
  display: inline-block;
}

.job-title {
  margin: 0;
  padding: 0;
  word-spacing: 10px;
  font-size: 40px;
  color: white;
  margin-left: 15px;
}

.job-title:hover {
  cursor: pointer;
  margin: 0;
  transition: 1.25s;
}

.second-container {
  background-color: rgb(214, 212, 212);
  margin: 0;
  padding: 0;
}

.second-container p {
  text-indent: 10px;
  margin-left: 20px;
  color: black;
  font-weight: 600;
  font-size: 25px;
}

.more-info-btn {
  width: 50px;
  height: 45px;
  background-color: #f25f4c;
  color: #0f0e17;
  border: none;
  margin-bottom: 5px;
  /* margin-top: 5px; */
  float: right;
  margin-right: 20px;
  /* margin-left: auto; */
  /* margin-right: 25px; */
}

.payment {
  margin: 0;
  padding: 0;
  font-size: 20px;
  color: white;
}

span {
  color: white;
}

p {
  color: white;
}

.sign {
  font-size: 20px;
  color: #f25f4c;
}

.past-jobs-title {
  background-color: white;
  text-align: center;
  font-size: 70px;
  margin: 0;
  padding-top: 190px;
}

.placeholder {
  display: flex;
  margin-top: 20px;
  background-color: #0f0e17;
}

.milestone-container {
  width: 40%;
  background: #f25f4c;
  margin: 10px;
  border-radius: 50px;
}

.milestone-title {
  text-align: center;
  color: #0f0e17;
}

.add-btn {
  width: 100%;
  height: 30px;
  margin-top: 5px;
  margin-bottom: 5px;
  color: #0f0e17;
  font-size: 20px;
  font-weight: 600;
  background-color: #f25f4c;
  border: none;
  border-radius: 5px;
}

.add-btn:hover {
  cursor: pointer;
}

.more-info-btn:hover {
  cursor: pointer;
}

.milestone-list-container {
  text-align: center;
}

.first-milestone {
  font-size: 50px;
  margin: 0;
  color: aliceblue;
  border-bottom: 2px solid#0f0e17;
}

.second-milestone {
  margin: 0;
  font-size: 30px;
  color: #0f0e17;
  margin-top: 10px;
}

.third-milestone {
  margin: 0;
  color: #0f0e17;
  margin-top: 15px;
}

.more-info-comps-container {
  display: flex;
  justify-content: space-evenly;
}

.individual-more-info-comps {
  background-color: #0f0e17;
  width: 27vw;
  height: 150px;
  justify-content: center;
  display: inline-block;
  align-items: center;
  text-align: center;
  border: 12px solid #f25f4c;
  border-radius: 200px;
  margin: 0;
  margin-top: 50px;
}

.more-info-title {
  margin-right: 50%;
  color: white;
}

.more-info-value {
  color: white;
}

.no-jobs-container {
  background-color: #0f0e17;
  color: white;
  text-align: center;
  padding: 0;
  margin: 0;
  padding-bottom: 50px;
}

.no-jobs-title {
  margin: 0;
}

.search-container {
  display: flex;
  justify-content: center;
  margin-bottom: 15px;
}

.search-bar {
  height: 30px;
  width: 250px;
  font-size: 20px;
}
